*{
  box-sizing:border-box;
  padding:0;
  margin:0;
  font-family:"josefin","Arial Narrow",Arial,sans-serif;

}

.main_div {
  width:100%;
  height: 100vh;
  background:violet;
  display:flex;
  flex-direction:row;
  justify-content: center;
  align-items:center;
  text-align:center;

}

.center_div{
  width:25%;
  height:80vh;
  background-color: white;
  box-shadow:5px 5px 25px -5px rgba(0, 0,0, 0.5);
  border-radius:15px;
}

h1{
  color:white;
  background:transparent;
  background-color:rgb(114, 195, 226);
  padding:6px 0 2px 0;
  margin-bottom:10px;

}

input{
  text-align:center;
  height:30px;
  top:10px;
  font-size:20px;
  font-weight:500;
  width:60%;
  border-bottom:2px solid purple;
    outline:none;
}

button {
  min-height:40px;
  width:40px;
  font-size:40px;
  font-color:white;
  border-radius:50%;
  background-color: skyblue;
  border-color:skyblue;
  margin-left:10px;
  box-shadow:5px 5px 15px-5px rgba(0,0,0,0.3);


}

button:hover{
  background-color:#20bf6b;
}

ol {
  margin-top:30px;

}

ol li {
  padding-left: 0px;
  text-align:center;
  font-size:20px;
  font-weight:500;
  min-height:40px;
  display:flex;
  align-items:center;
  color:#8566aa;
  text-transform: capitalize;
}

.todo_style {
  display:flex;
  flex-direction:row;
  justify-content:left;
  align-items:center;
}

.todo_style .fa-times {
  width: 20px;
  height: 20px;
  display:flex;
  justify-content: center;
  align-items: center;
  background-color: #8566aa;
  border-radius: 50%;
  margin: 0 15px 0 35px;
  color:rgb(56, 112, 160);
  box-shadow:5px 5px 15px -5px rgba(0,0,0,0.3);
}

.todo_style .fa-times:hover {
  background-color:crimson;
}

.todo_style .fa-times:hover .list_style {
  background-color:crimson;
}

@media (max-width: 768px){
  .center_div{
    width:55%;
  }

  .todo_style .fa-times {
    margin: 0 15px 0 15px;
    display:flex;
    justify-content:center;
    align-items:center;
    background-color: slateblue;
}
button {
  min-height:40px;
  margin-left:5px;
 }
}
